@import url(https://fonts.googleapis.com/css2?family=Epilogue:wght@500&family=Nunito:wght@600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}
html[data-theme="light"] {
  --background-color: #f0f0f0;
  --alternate-background-colour: #f7f7f9;
  --text-color: rgba(18, 20, 22, 0.85);
  --btn-color: #d9239f;
  --btn-text: #fff;
  --btn-color-1: #4928de;
  --nav-bg: #000000;
  --skill-box-shadow: grey;
}
html[data-theme="dark"] {
  --background-color: #212a2e;
  --alternate-background-colour: #263238;
  --text-color: #f7f8f8;
  --btn-color: #d9239f;
  --btn-text: black;
  --btn-color-1: #61dafb;
  --nav-bg: #f7f8f8;
  --skill-box-shadow: black;
}

#home {
  height: 93vh;
  background-color: var(--background-color);
  scroll-margin-top: 4rem;
  transition: 0.7s;
  color: var(--text-color);
  align-items: center;
  justify-content: space-evenly;
  display: flex;
}
#home .txtbox {
  font-family: "Nunito", sans-serif;
}
#home h2 {
  font-weight: 500;
  font-size: 5vw;
  line-height: 3rem;
}
#home h2 span {
  font-weight: bolder;
  font-size: 3vw;
}
#home h3 {
  font-weight: bold;
  font-size: 1.2rem;
}
.heading {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 8px;
}
.svgimage {
  width: 500px;
  height: auto;
}
@media only screen and (max-width: 1008px) {
  .svgimage {
    height: 300px;
    width: 300px;
  }
}
@media only screen and (max-width: 768px) {
  #home h2 {
    font-size: 4em;
  }
  #home h2 span {
    font-size: 2.6rem;
  }
}
@media only screen and (max-width: 688px) {
  #home {
    height: 100vh;
    flex-direction: column;
    scroll-margin-top: 0;
  }
  #home .txtbox {
    margin: auto;
    margin-top: 100px;
    margin-bottom: 0;
  }
}

.card {
  margin: 20px;
  box-shadow: 0 0 6px 0px black;
  width: 300px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  background-color: var(--alternate-background-colour);
}
.card-image {
  max-width: 300px;
}
.card-body {
  margin: 5px 10px;
  padding: 5px 10px;
}
.card-body > h2 {
  text-align: center;
}
.card-body > p {
  text-align: start;
}
.projectbutton {
  text-decoration: none;
  background-color: var(--btn-color-1);
  margin: 10px 0;
  width: 100%;
  text-align: center;
  color: var(--btn-text);
  padding: 8px;
  font-size: 16px;
  display: block;
  border-radius: 7px;
  border: none;
  border: 1px solid transparent;
}
.projectbutton:hover{
  background-color: var(--background-color);
  color: var(--btn-color-1);
  border-color:  var(--btn-color-1);
}
#project {
  color: var(--text-color);
  transition: 0.7s;
  scroll-margin-top: 4rem;
  background-color: var(--background-color);
}

#skills {
  padding-top: 70px;
  background-color: var(--background-color);
  color: var(--text-color);
  transition: 0.7s;
}
#skills h1 {
  padding: 20px 0px;
}
.skill-cards {
  width: 80%;
  display: grid;
  margin: auto;
  grid-template-columns: repeat(2, 1fr);
}
.skill-cards .info-box {
  background-color: var(--alternate-background-colour);
  margin: 10px;
  padding: 8px;
}
.skill-cards .info-box h2 {
  text-align: center;
  padding: 5px;
  font-family: "Epilogue", sans-serif;
}
.skill-cards .info-box p {
  padding: 10px;
  text-align: center;
  font-family: "Nunito", sans-serif;
}
.skill-cards .info-box .frontend,
.backend {
  margin: 20px 0;
}
.skill {
  padding: 10px 5px;
  margin: 10px;
  box-shadow: 0px 10px 18px 1px var(--skill-box-shadow);
}
@media only screen and (max-width: 768px) {
  .skill-cards {
    width: 100%;
    display: block;
  }
  .skill-cards .info-box:last-child {
    margin-bottom: 0px;
  }
}

.skill .decoration {
  display: flex;
}
.skill .decoration .skill-name {
  width: 128px;
}
.skill .decoration .status {
  background-color: rgb(218, 204, 204);
  border-radius: 10px;
  width: 100%;
  height: 12px;
  margin-top: 3px;
}
.skill .decoration .status .statusbar-html {
  background: linear-gradient(45deg, rgb(13, 216, 206), rgb(97, 9, 156));
  border-radius: 10px;
  height: 100%;
}
.skill .decoration .status .statusbar-css {
  background: linear-gradient(45deg, rgb(204, 218, 8), rgb(218, 14, 184));
  border-radius: 10px;
  height: 100%;
}
.skill .decoration .status .statusbar-react {
  border-radius: 10px;
  background: linear-gradient(45deg, rgb(38, 248, 18), rgb(8, 216, 154));
  height: 100%;
}
.skill .decoration .status .statusbar-redux {
  border-radius: 10px;
  background: linear-gradient(45deg, rgb(18, 248, 198), rgb(216, 202, 8));
  height: 100%;
}
.skill .decoration .status .statusbar-typescript {
  background: linear-gradient(45deg, rgb(250, 28, 250), rgb(67, 19, 122));
  border-radius: 10px;
  height: 100%;
}
.skill .decoration .status .statusbar-javascript {
  background: linear-gradient(45deg, rgb(83, 248, 68), rgb(8, 22, 216));
  border-radius: 10px;
  height: 100%;
}
.skill .decoration .status .statusbar-node {
  background: linear-gradient(45deg, rgb(204, 218, 8), rgb(218, 14, 184));
  border-radius: 10px;
  height: 100%;
}
.skill .decoration .status .statusbar-mysql {
  background: linear-gradient(45deg, rgb(83, 248, 68), rgb(8, 22, 216));
  border-radius: 10px;
  height: 100%;
}
.skill .decoration .status .statusbar-mongo {
  background: linear-gradient(45deg, rgb(13, 216, 206), rgb(97, 9, 156));
  border-radius: 10px;
  height: 100%;
}
.skill .decoration .status .statusbar-rest {
  background: linear-gradient(45deg, rgb(38, 248, 18), rgb(8, 216, 154));
  border-radius: 10px;
  height: 100%;
}
.skill .decoration .status .statusbar-express {
  background: linear-gradient(45deg, rgb(250, 28, 250), rgb(67, 19, 122));
  border-radius: 10px;
  height: 100%;
}
.skill .decoration .status .statusbar-flask {
  background: linear-gradient(45deg, rgb(250, 28, 58), rgb(76, 19, 122));
  border-radius: 10px;
  height: 100%;
}
.skill .decoration span {
  margin: 0 12px;
}

#contact {
  color: var(--text-color);
  transition: 0.7s;
  scroll-margin-top: 4rem;
  background-color: var(--background-color);
}
label,
input {
  color: var(--text-color);
  display: block;
  width: -webkit-fill-available;
  font-size: 18px;
}
label {
  margin: 10px 0;
}
input {
  padding: 5px 10px;
}
textarea {
  width: -webkit-fill-available;
  padding: 5px 10px;
  font-size: 18px;
}
.contact-form {
  margin: auto;
  width: 33%;
  padding: 10px;
  border: 2px solid var(--text-color);
}
.result {
  color: green;
  font-size: 18px;
  text-align: center;
  margin: 10px 0;
  font-weight: bold;
}
.bold {
  font-weight: bold;
  font-size: 18px;
}
@media only screen and (max-width: 688px) {
  .contact-form {
    width: 85%;
  }
}

#about {
  padding: 35px 0px;
  color: var(--text-color);
  transition: 0.7s;
  scroll-margin-top: 3rem;
  background-color: var(--background-color);
}
#about .details {
  width: 80%;
  display: flex;
  margin: auto;
}
#about .details p {
  padding-top: 20px;
  text-align: left;
  padding-right: 30px;
  font-family: "Epilogue", sans-serif;
  font-size: 17px;
  line-height: 1.6rem;
}
#about .details .resume {
  text-decoration: none;
  background-color: rgb(230, 18, 106);
  color: white;
  font-size: 17px;
  border: none;
  border-radius: 6px;
  padding: 10px;
  margin-right: 10px;
}
#about .details .resume:hover {
  color: rgb(230, 18, 106);
  background-color: white;
  border: solid 2px rgb(230, 18, 106);
}
#about .details img {
  position: relative;
  border: 0px solid black;
  border-radius: 50%;
  width: 23em;
  height: 23em;
  z-index: 3;
}
#about .details div {
  position: relative;
  width: 23em;
  height: 23em;
  border-radius: 50%;
  padding: 7px;
}
#about .details div:hover:after {
  -webkit-animation: animate 1s linear infinite;
          animation: animate 1s linear infinite;
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  border-radius: 50%;
  background: linear-gradient(45deg, transparent, transparent 40%, #e5f403);
}
@-webkit-keyframes animate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    -webkit-filter: hue-rotate(0deg);
            filter: hue-rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    -webkit-filter: hue-rotate(360deg);
            filter: hue-rotate(360deg);
  }
}
@keyframes animate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    -webkit-filter: hue-rotate(0deg);
            filter: hue-rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    -webkit-filter: hue-rotate(360deg);
            filter: hue-rotate(360deg);
  }
}
@media only screen and (max-width: 1008px) {
  #about .details img {
    border: 0px solid black;
    border-radius: 50%;
    width: 15em;
    height: 15em;
  }
  #about .details div {
    position: relative;
    width: 15em;
    height: 15em;
    border-radius: 50%;
    padding: 7px;
  }
}
@media only screen and (max-width: 688px) {
  #about {
    scroll-margin-top: 0;
  }
  #about .details {
    display: block;
  }
}

nav {
  background-color: black;
  color: white;
  position: -webkit-sticky;
  position: sticky;
  left: 0px;
  display: flex;
  top: 0px;
  transition: 0.3s;
  z-index: 5;
  padding: 7px;
}
.navbar h1 {
  margin: 10px;
  font-family: "Epilogue", sans-serif;
}
nav ul {
  display: flex;
  margin: auto;
}
nav ul li {
  padding: 10px 7px;
  margin: 0px 30px;
  list-style: none;
}
.darkmode {
  position: fixed;
  cursor: pointer;
  top: 15px;
  z-index: 6;
  right: 20px;
  display: flex;
  width: 60px;
  height: 30px;
  justify-content: center;
  border-radius: 30px;
  align-items: center;
  background-color: #4d4d4d;
}
.coverarea {
  position: absolute;
  left: 5px;
  width: 28px;
  height: 27px;
  background-color: white;
  border-radius: 50%;
  transition: 0.3s;
}
.toggleball {
  left: 29px;
}
.fa-sun {
  font-size: 18px;
  color: yellow;
  margin-right: 10px;
}
.fa-moon {
  font-size: 18px;
  color: yellow;
}
.navbar ul li a {
  text-decoration: none;
  color: white;
  font-size: 21px;
  font-family: "Epilogue", sans-serif;
}
.navbar.sticky {
  background-color: white;
  color: black;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);
}
.navbar.sticky ul li a {
  color: black;
}
.navbar .burgerbox {
  position: absolute;
  display: none;
  left: 20px;
  top: 9px;
  background-color: transparent;
  height: 30px;
  width: 40px;
  padding: 4px;
  border-radius: 4px;
  transition: 0.7s;
}
.burgerbox .burgerlines {
  background-color: white;
  height: 5px;
  width: 38px;
  margin: 4px 0px;
  transition: 0.5s;
}
@media only screen and (max-width: 688px) {
  nav {
    display: block;
    height: 50px;
  }
  nav ul {
    display: block;
    position: absolute;
    top: 4rem;
    background-color: var(--nav-bg);
    width: 70vw;
    height: 100vh;
    left: -70vw;
    transition: 0.5s;
  }
  nav ul li {
    text-align: center;
    margin: 10px;
  }
  .darkmode {
    top: 14px;
  }
  .navbar h1 {
    display: none;
  }
  .navbar.sticky {
    background-color: black;
  }
  .navbar.sticky ul li a, .navbar ul li a{
    color: var( --btn-text);
  }
  .navbar .burgerbox {
    display: block;
  }
  .toggle {
    left: 0;
  }
  .rotatef {
    -webkit-transform: rotate(45deg) translateY(6px);
            transform: rotate(45deg) translateY(6px);
  }
  .rotatel {
    -webkit-transform: rotate(-45deg) translateY(-5px);
            transform: rotate(-45deg) translateY(-5px);
  }
  .remove {
    display: none;
  }
}

.sidebar {
  position: fixed;
  top: 40%;
  right: 0;
  z-index: 2;
}
.sidebar > li {
  list-style: none;
}
.sidebar > li > a {
  text-decoration: none;
}
.twitter,
.hashnode,
.linkedin,
.github,
.instagram {
  padding: 10px;
  text-align: center;
}
.linkedin,
.github {
  border-bottom: 1px solid black;
}
.linkedin {
  background-color: #0e76a8;
}
.github {
  background-color: #211f1f;
}
.twitter {
  background-color: #1da1f2;
}
.hashnode {
  background-color: white;
}
.instagram {
  background-color: #cd486b;
}
.hashnode-icon {
  width: 30px;
  height: 30px;
}
.icon {
  font-size: 26px;
  color: white;
}

.footer {
  background-color: var(--background-color);
  color: var(--text-color);
  transition: 0.7s;
  text-align: center;
  padding: 10px 0;
}

